import React ,{useEffect,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import axios from "axios";
import {
    Grid,
 
} from '@material-ui/core/'
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});
const BASE_URL="https://jsonplaceholder.typicode.com/"
export default function PostCommentsList(props) {
    
  const classes = useStyles();

  const [pictures, setPictures] = useState();
  const [post, setPost] = useState();
  useEffect(() => {
    axios.get(`${BASE_URL}posts/${props.match.params.id}/comments`)
            .then(response => { 
                 console.log(response.data)
                 setPictures(response.data)
               
            })
             .catch(error => console.log(error.response));
    
  }, [])
  useEffect(() => {
    axios.get(`${BASE_URL}posts/${props.match.params.id}/`)
            .then(response => { 
                 console.log(response.data)
                 setPost(response.data.title)
               
            })
             .catch(error => console.log(error.response));
    
  }, [])
  return (
      <>
      {console.log(props.match.params.id)}
      <Typography variant="h2">Comments on Post: <strong style={{color:'red'}}>{post && post}</strong></Typography>
    <Grid
    container
    spacing={2}
    direction="row"
    justify="flex-start"
    alignItems="flex-start"
>
{
                  pictures &&  pictures.map((picture)=>(
<Grid item xs={12} sm={6} md={3} key={picture.id}>
    <a href={picture.url}>
    <Card className={classes.root}>
      <CardActionArea>
      
        <CardContent>
        <Typography gutterBottom variant="subtitle2" component="h2">
           <strong style={{color:'Blue'}}>Name :</strong> {picture.name}
          </Typography>
          <Typography gutterBottom variant="h5" component="h2">
          <strong style={{color:'Blue'}}>Email :</strong> {picture.email}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
          <strong style={{color:'Blue'}}>Comment :</strong> {picture.body}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
        <Button size="small" color="primary">
          Learn More
        </Button>
      </CardActions>
    </Card>
    </a>
    </Grid>
        ))
    } 
    </Grid>
    </>
  );
}
